@media (max-width: 480px) {
  .intro {
    display: flex;

    min-height: 90vh;
    height: 100%;
    background-color: #171d20;
    margin-top: 60px;
    .intro-text {
      display: flex;
      padding: 7% 0;
      width: 100%;
    }
    .intro-heading {
      font-size: 2rem;
      margin: 0 0 1% 0;
    }
    .intro-subheading {
      font-size: 1.7rem;
      margin: 0 0 0 0;
    }

    .intro-visual {
      margin-top: 2%;
      img {
        width: 250px;
      }
    }
    .hilight {
      color: $accent1;
      opacity: 1;
      text-shadow: 2px 2px 1px black;
    }
  }
}
